<template>
    <div class="mypage-container">
        <div class="mypage-title pc-element">활동정보 보기</div>
        <div class="profile-box">
            <div class="img-box">
                <img v-if="memImg" :src="memImg">
                <font-awesome-icon v-else class="icon" icon="fa-solid fa-user"/>
            </div>
            <div class="user-name-box">
                <div class="user-name">{{uid}}</div>
                <v-btn class="whiteButton" depressed @click="goModify()"><span>나의 계정</span></v-btn>
            </div>
        </div>
        <div class="article-total-box">
            <ul class="tab">
                <li v-for="tab in tabList" :class="tab.active" @click="setTab(tab.code)">{{tab.name}}</li>
            </ul>
            <div v-if="tabCode=='latest'">
                <ul class="latest-box" v-if="tabCode=='latest'">
                    <li class="item-li" v-for="item in memberNoticeList" @click="goViewArticle(item)">
                        <div class="content-box">
                            <span class="cmtspan" v-if="item.gtype=='comment'">[댓글]</span>
                            <span class="namespan" v-if="item.gtype=='comment'">{{item.userName}}</span>
                            {{item.noticeContent}}
                        </div>
                        <div class="date">{{item.createAt.replace('T',' ').substring(0,16)}}</div>
                    </li>
                </ul>
                <div class="moreButton-box" v-if="memberOrgNoticeList.length>5">
                    <v-btn depressed class="whiteButton" @click="moreMemberNotice()"><span>{{moreText}}</span></v-btn>
                </div>
            </div>
            <div class="article-box" v-else-if="tabCode=='article'">
                <ul class="article-list">
                    <li class="pc-element">
                        <div class="subject-content theader">
                            <div class="number">
                                번호
                            </div>

                            <div class="main">
                                <div class="subject">
                                    제목
                                </div>
                                <div class="author">
                                    글쓴이
                                </div>
                                <div class="date">
                                    날짜
                                </div>
                                <div class="hit">
                                    조회수
                                </div>
                            </div>

                        </div>
                    </li>
                    <li v-for="(item,index) in articleList" @click="articleView(item)">
                        <div class="subject-content">
                            <div class="number  pc-element">
                                {{ item.number }}
                            </div>
                            <div class="main">
                                <div class="subject">
                                    <div class="subject-text">{{ item.subject }}</div>
                                    <div class="span paddng-left" v-if="item.commentCnt>0">[{{item.commentCnt}}]</div>
                                    <div class="span paddng-left" v-if="item.dfile"><img
                                            src="../../../assets/img/file.gif"></div>

                                </div>
                                <div class="author-box">
                                    <div class="author"><span class="tablet-mobile-element">작성자</span>{{ item.userName
                                        }}
                                    </div>
                                    <div class="date"><span class="tablet-mobile-element">작성일자</span>{{
                                        item.createAt.substring(0, 10)}}
                                    </div>
                                    <div class="hit pc-element">
                                        {{item.hit}}
                                    </div>
                                </div>
                            </div>

                        </div>
                    </li>
                </ul>
                <paging :paging="paging" :reload="pagingReload" @pageMove="pageMode"></paging>
            </div>
        </div>
    </div>
</template>
<script>
    import apiService from '@/config/web/apiService'
    import {
        API_URL_GET_MYPAGE_MAIN,
        API_URL_GET_MYPAGE_ARTICLE
    } from '@/config/web/urls'
    import AUTH from '@/config/web/auth'
    import paging from "@/components/paging";
    import {mapGetters} from "vuex";

    export default {
        name: 'memberMain',
        computed: {
            ...mapGetters('config', ["getMenu", "getSettingActive"]),
        },
        components: {
            paging
        },
        watch: {
            getSettingActive(val) {
                if (val) {
                    this.getMcode();
                }
            }
        },
        data() {
            return {
                memImg: '',
                uid: '',
                tabCode: 'latest',
                tabList: [
                    {name: '최근 활동', code: 'latest', active: 'active'},
                    {name: '게시물', code: 'article', active: ''},
                ],
                articleList: [],
                limit: 20,
                page: 1,
                pageGroup: 5,
                total: 0,
                paging: {},
                pagingReload: false,
                boardIds: {},
                memberNoticeList: [],
                memberOrgNoticeList: [],
                moreText: '펼쳐보기',
            };
        },
        created() {
            const member = AUTH.getSession();
            if (member && member?.memberInfo) {
                this.getData();
                if (this.getSettingActive) {
                    this.getMcode();
                }
            } else {
                this.$router.push('/');
            }

        },
        methods: {
            getData() {
                apiService.post(API_URL_GET_MYPAGE_MAIN, {
                    page: this.page.toString(),
                    limit: this.limit.toString()
                }, (res) => {

                    if (res.status == 'success') {
                        const memberInfo = res.memberInfo;
                        this.uid = memberInfo.uid;
                        if (memberInfo.img) {
                            this.memImg = apiService.imgHost + 'api/controller/member/getMemberImage?imgName=' + encodeURIComponent(memberInfo.img)
                        }
                        this.total = res.articleList.total
                        this.paging = {
                            total: this.total,
                            currentPage: this.page,
                            limit: this.limit,
                            pageGroup: this.pageGroup,
                        }
                        let start = this.total - ((this.page - 1) * this.limit);
                        let k = 0;
                        for (let item of res.articleList.list) {
                            item.number = start - k;
                            this.articleList.push(item)
                            k++;
                        }
                        this.pagingReload = true;
                        setTimeout(() => {
                            this.pagingReload = false;
                        }, 100)

                        if (res.memberNoticeList) {
                            let k = 0;
                            for (let item of res.memberNoticeList.content) {
                                item.noticeContent = (item.gtype == 'comment') ? '님이 내글에 새로운 댓글을 달았습니다' : '새 공지사항이 등록되었습니다';
                                this.memberOrgNoticeList.push(item);
                                if (k < 5) {
                                    this.memberNoticeList.push(item);
                                }
                                k++;

                            }

                        }

                    }
                })
            },
            moreMemberNotice() {
                if (this.memberNoticeList.length == this.memberOrgNoticeList.length) {
                    this.moreText = '펼쳐보기'
                    this.memberNoticeList = [];
                    let k = 0;
                    for (const item of this.memberOrgNoticeList) {
                        if (k < 5) {
                            this.memberNoticeList.push(item)
                        }
                        k++;

                    }
                } else {
                    this.moreText = '접기'
                    this.memberNoticeList = [];
                    for (const item of this.memberOrgNoticeList) {
                        this.memberNoticeList.push(item)
                    }

                }


            },
            getMcode() {

                for (let menu of this.getMenu) {
                    if (menu.muse != 'yes') continue;
                    if (menu.subList && menu.subList.length > 0) {
                        for (let subMenu of menu.subList) {
                            if (subMenu.muse != 'yes') continue;
                            if (subMenu.linkType == 'board') {
                                this.boardIds[subMenu.boardId] = {mainCode: menu.code, subCode: subMenu.code};
                            }
                        }

                    } else {
                        if (menu.linkType == 'board') {
                            this.boardIds[menu.boardId] = {mainCode: menu.code};
                        }
                    }
                }

            },
            goViewArticle(item) {

                if (item.gtype == 'comment') {
                    if (this.boardIds[item.bid]?.mainCode) {
                        sessionStorage.setItem('viewComment', item.gid);
                        const subCode = (this.boardIds[item.bid].subCode) ? this.boardIds[item.bid].subCode : 'none';
                        this.$router.push('/board/articleView/' + this.boardIds[item.bid].mainCode + '/' + subCode + '/' + item.parentId)
                    }
                } else {
                    if (this.boardIds[item.bid]?.mainCode) {
                        const subCode = (this.boardIds[item.bid].subCode) ? this.boardIds[item.bid].subCode : 'none';
                        this.$router.push('/board/articleView/' + this.boardIds[item.bid].mainCode + '/' + subCode + '/' + item.gid)
                    }
                }

            },
            goModify() {
                this.$router.push('/mypage/memberModify')
            },
            getArticleList() {
                apiService.post(API_URL_GET_MYPAGE_ARTICLE, {
                    page: this.page.toString(),
                    limit: this.limit.toString()
                }, (res) => {

                    if (res.status == 'success') {
                        this.total = res.articleList.total
                        this.paging = {
                            total: this.total,
                            currentPage: this.page,
                            limit: this.limit,
                            pageGroup: this.pageGroup,
                        }
                        let start = this.total - ((this.page - 1) * this.limit);
                        let k = 0;
                        this.articleList = [];
                        for (let item of res.articleList.list) {
                            item.number = start - k;
                            this.articleList.push(item)
                            k++;
                        }
                        this.pagingReload = true;
                        setTimeout(() => {
                            this.pagingReload = false;
                        }, 100)
                    }
                })
            },
            setTab(code) {
                this.tabList.forEach((item) => {
                    if (item.code == code) {
                        item.active = 'active'
                        this.tabCode = code;
                    } else {
                        item.active = ''
                    }
                })

            },
            pageMode(params) {
                this.page = params.page;
                this.getArticleList();
            },
            articleView(item) {
                if (this.boardIds[item.bid]?.mainCode) {
                    const subCode = (this.boardIds[item.bid].subCode) ? this.boardIds[item.bid].subCode : 'none';

                    this.$router.push('/board/articleView/' + this.boardIds[item.bid].mainCode + '/' + subCode + '/' + item.id)
                }
            },

        }

    }
</script>


<style lang="scss" scoped>
    .mypage-container {
        width: 100%;
        margin: 0;
        padding: 21px 24px;
        min-height: calc(100vh - 300px);
        background: #fff;

        .mypage-title {
            font-size: 18px;
            font-weight: bold;
            color: #333333;
            border-bottom: solid 1px #333;
            padding-bottom: 10px;
        }

        .profile-box {
            padding: 50px 0 30px 0;
            text-align: center;
            border-bottom: solid 1px #ddd;

            .img-box {
                width: 96px;
                height: 96px;
                overflow: hidden;
                border: solid 1px #ddd;
                border-radius: 50%;
                margin: auto;

                .icon {
                    font-size: 40pt;
                    color: #ddd;
                    margin-top: 18px;
                }

                img {
                    width: 100%;
                    min-height: 96px;
                }
            }

            .user-name {
                font-size: 18px;
                color: #333;
                text-align: center;
                font-weight: 600;
                margin: 5px 0;
            }

            .whiteButton {
                width: 118px;
                height: 36px;
                margin: auto;
                max-height: 36px;

                span {
                    font-size: 13px !important;
                }
            }
        }

        .article-total-box {
            margin: 40px 0;

            .tab {
                padding: 0;
                display: flex;
                border-bottom: solid 1px #333;
                margin-bottom: 30px;

                li {
                    height: 38px;
                    line-height: 38px;
                    padding: 0 20px;
                    border-right: solid 1px #333;
                    border-top: solid 1px #333;
                    cursor: pointer;
                    font-size: 14px;

                    &:first-child {
                        border-left: solid 1px #333;
                    }

                    &.active {
                        background: #333;
                        color: #fff;
                    }
                }
            }

            .latest-box {
                width: 100%;
                padding: 0 20px;

                .item-li {
                    color: #333;
                    height: 36px;
                    line-height: 36px;
                    padding: 0;
                    cursor: pointer;
                    margin: 0;
                    text-align: left;
                    display: flex;
                    justify-content: space-between;

                    .content-box {
                        font-size: 14px;
                        color: #333;

                        span.cmtspan {
                            color: #6446C2;
                            font-weight: bold;
                            font-size: 14px;
                        }

                        span.namespan {
                            color: #333;
                            font-weight: bold;
                            padding-left: 10px;
                            font-size: 14px;
                        }
                    }

                    .date {
                        font-size: 14px;
                        color: #999999;

                    }
                }
            }

            .moreButton-box {
                padding-top: 50px;

                .whiteButton {
                    max-height: 45px !important;

                    span {
                        font-size: 14px;
                    }
                }
            }

            .article-box {
                width: 100%;
                padding: 0;

                .article-list {
                    padding: 0;
                    margin: 0;

                    li {
                        padding: 0;
                        cursor: pointer;
                        height: 50px;

                        .subject-content {
                            border-bottom: solid 1px #dcdcdc;
                            display: flex;

                            &:hover {
                                border-top: solid 1px #dcdcdc;
                                background: #fafafa;
                            }

                            .number {
                                min-width: 50px;
                                width: 50px;
                                font-size: 14px;
                                line-height: 50px;
                                color: #333;
                                text-align: center;

                            }

                            .category {
                                min-width: 100px;
                                width: 100px;
                                font-size: 14px;
                                line-height: 50px;
                                color: #000000;
                                text-align: center;
                            }

                            .main {
                                width: 95%;
                                display: flex;

                                .icon {
                                    font-size: 14px;
                                    margin: 3px 10px 0 0;
                                }

                                .subject {
                                    padding-left: 10px;
                                    font-size: 14px;
                                    line-height: 50px;
                                    height: 50px;
                                    color: #333;
                                    margin: 0;
                                    width: 95%;
                                    display: flex;
                                    justify-content: left;

                                    .subject-text {
                                        color: #333;
                                        max-height: 50px;
                                        white-space: nowrap;
                                        overflow: hidden;
                                        -webkit-line-clamp: 1;
                                        text-overflow: ellipsis;
                                        white-space: normal;
                                        word-wrap: break-word;
                                        display: -webkit-box;
                                        text-align: left;
                                        -webkit-box-orient: vertical;
                                    }

                                    .span {
                                        font-size: 14px;
                                        color: #333;
                                        padding-right: 5px;

                                        &.paddng-left {
                                            padding-left: 3px;
                                        }
                                    }
                                }

                                .author-box {
                                    display: flex;

                                    .author {
                                        min-width: 100px;
                                        font-size: 14px;
                                        line-height: 50px;
                                        color: #333;
                                        text-align: center;
                                    }

                                    .date {
                                        min-width: 100px;
                                        font-size: 14px;
                                        line-height: 50px;
                                        color: #333;
                                        text-align: center;
                                    }

                                    .hit {
                                        min-width: 50px;
                                        font-size: 14px;
                                        line-height: 50px;
                                        color: #333;
                                        text-align: center;
                                    }
                                }

                            }

                            &.theader {
                                border-bottom: solid 1px #cacaca;
                                border-top: solid 1px #cacaca;

                                &:hover {
                                    border-top: solid 1px #cacaca;
                                    background: #fff;
                                }

                                .number {
                                    font-weight: bold;

                                }

                                .category {
                                    font-weight: bold;
                                    line-height: 50px;


                                }

                                .main {
                                    .subject {
                                        text-align: center;
                                        font-weight: bold;


                                    }

                                    .author {
                                        min-width: 100px;
                                        font-size: 14px;
                                        line-height: 50px;
                                        color: #333;
                                        text-align: center;
                                        font-weight: bold;

                                    }

                                    .date {
                                        min-width: 100px;
                                        font-size: 14px;
                                        line-height: 50px;
                                        color: #333;
                                        text-align: center;
                                        font-weight: bold;

                                    }

                                    .hit {
                                        min-width: 50px;
                                        font-size: 14px;
                                        line-height: 50px;
                                        color: #333;
                                        text-align: center;
                                        font-weight: bold;

                                    }

                                }
                            }

                        }


                    }

                }
            }
        }

    }

    @media screen and (max-width: 1259px) {
        .mypage-container {
            padding: 0;

            .profile-box {
                padding: 30px 0;
                display: flex;

                .img-box {
                    margin-left: 10px;
                    min-width: 96px;
                }

                .user-name-box {
                    width: 90%;
                    text-align: left;
                    padding-left: 30px;

                    .user-name {
                        font-size: 18px;
                        color: #333;
                        text-align: left;
                        font-weight: 600;
                        margin: 5px 0;
                    }

                    .whiteButton {
                        width: 118px;
                        height: 36px;
                        margin: auto;
                        max-height: 36px;

                        span {
                            font-size: 13px !important;
                        }
                    }
                }
            }

            .article-total-box {
                .tab {
                    margin-bottom: 0;

                }

                .latest-box {
                    width: 100%;
                    padding: 10px 10px;

                    .item-li {
                        height: auto;
                        line-height: 120%;
                        padding: 10px 0;
                        display: block;

                        .date {
                            padding-top: 6px;
                            font-size: 14px;
                            color: #999999;

                        }
                    }
                }

                .article-box {
                    .article-list {
                        li {
                            padding: 0;
                            cursor: pointer;
                            height: auto !important;

                            .subject-content {
                                padding: 10px 0;

                                .main {
                                    width: 100%;
                                    display: block !important;

                                    .subject {
                                        line-height: 30px !important;
                                        height: auto !important;
                                        max-height: 30px !important;

                                        .subject-text {
                                            max-height: 30px !important;
                                        }
                                    }

                                    .author-box {
                                        padding-top: 5px;

                                        .author {
                                            min-width: auto !important;
                                            line-height: 100% !important;
                                            color: #333;
                                            padding-left: 10px;
                                            text-align: left !important;

                                            span {
                                                color: #999;
                                                padding-right: 5px;
                                            }
                                        }

                                        .date {
                                            padding-left: 10px;
                                            min-width: auto !important;
                                            line-height: 100% !important;

                                            color: #333;

                                            span {
                                                color: #999;
                                                padding-right: 5px;
                                            }
                                        }

                                    }

                                }

                            }


                        }

                    }
                }
            }
        }
    }

    @media screen and (max-width: 450px) {
        .mypage-container {
            padding: 0 20px;


        }
    }
</style>